.cart-modal {
  border-radius: 4px;
  padding: var(--main-offset);
  width: 580px;

  .modal__close {
    display: none;

    @include small-tablet {
      z-index: 100;
      display: block;
      border-radius: 100%;
      background-color: tomato;
    }
  }

  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: var(--main-offset);

    @include small-tablet {
      display: block;
    }
  }

  &__title {
    margin: 0;
    font: var(--prehigh-regular);
    color: var(--color-main);
  }

  &__order-number {
    font: var(--small-book);
    line-height: 14px;
    color: var(--color-grey);
  }

  &__input {
    color: var(--color-grey);
    background-color: #f6f6f6;

    &:not(:last-child) {
      margin-bottom: var(--small-offset);
    }
  }

  @include small-tablet {
    width: 100%;
    max-width: 570px;
  }
}

.cart-modal-order {
  margin-bottom: var(--main-offset);
  border: 1px solid #d9d9d9;
  border-radius: 4px;
  padding: 30px;

  &__top {
    font: var(--middle-book);
    line-height: 16px;
    color: var(--color-grey);

    span {
      font-weight: 700;
      color: var(--color-main);
    }

    @include mobile {
      font-size: 14px;
    }
  }

  &__list {
    display: none;

    &--visible {
      display: block;
    }
  }

  &__quantity {
    margin-bottom: var(--second-offset);
  }

  &__summ {
    margin-bottom: var(--second-offset);
  }

  .mini-cart__item {
    :not(:last-child) {
      margin-bottom: 14px;
    }
  }

  &__show {
    display: inline-flex;
    align-items: center;
    margin-bottom: var(--second-offset);
    color: var(--color-main);
    transition: transform 0.3s ease-in-out;

    svg {
      margin-left: 5px;
      width: 7px;
      height: 7px;
      fill: currentColor;
      object-fit: cover;
    }

    &--active {
      svg {
        transform: rotate(180deg);
      }
    }
  }
}
